<template>
  <div id="contact">
    <h1>連絡我們</h1>
    <div class="contact__container">
      <div class="preface">
        <div class="preface__heart">
          <img src="../../assets/icon/gold_heart_icon.png" alt="">
        </div>
        <div class="preface__text">
          <p>請填寫所有資料，我們將會儘速與您聯絡</p>
          <p>也可以點擊以下使用Line或Facebook留言與我們聯絡。</p>
        </div>
        <ul class="preface__sharelinks">
          <li>
            <a href="http://line.me/ti/p/@goodafter.tw">
              <img src="../../assets/icon/line_logo.png" alt="">
            </a>
          </li>
          <li>
            <a href="http://m.me/goodafter.tw">
              <img src="../../assets/icon/facebook_logo.png" alt="">
            </a>
          </li>
        </ul>
      </div>
      <form @submit="submit_form">
        <div class="form_group">
          <input required type="text" placeholder="姓名" v-model="form.name">
        </div>
        <div class="form_group">
          <input required type="tel" placeholder="聯絡電話" v-model="form.tel">
        </div>
        <div class="form_group">
          <input required type="email" placeholder="E-mail" v-model="form.mail">
        </div>
        <div class="form_group">
          <textarea required placeholder="留言給我們..." v-model="form.message"></textarea>
        </div>
        <div class="form_group captcha">
          <canvas class="canvas" width="75" height="40"></canvas>
          <input required type="text" v-model="input_code">
          <a href="#" @click.prevent="create_canvas">換一個</a>
        </div>
        <button type="submit" class="submit" title="送出">
          <img src="../../assets/icon/submit_contact.png" alt="">
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  props: ['mailbox'],
  data() {
    return {
      form: {
        id: '',
        name: '',
        tel: '',
        mail: '',
        message: '',
      },
      captcha_code: '',
      input_code: '',
    };
  },
  methods: {
    create_form() {
      this.form = {
        id: JSON.stringify(Date.now()),
        name: '',
        tel: '',
        mail: '',
        message: '',
      };
    },
    create_canvas() {
      const codelength = 4;
      const codebase = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
      let code = '';
      for (let index = 0; index < codelength; index += 1) {
        const codebaseIndex = parseInt(Math.floor(Math.random() * codebase.length), 10);
        const codeAlphabet = codebase[codebaseIndex];
        code += codeAlphabet;
      }
      this.captcha_code = code;
      const canvas = document.querySelector('.canvas');

      // 宣告為2D繪圖
      const canvas2d = canvas.getContext('2d');

      // 繪製背景色
      canvas2d.fillStyle = 'white';
      canvas2d.fillRect(0, 0, 100, 100);

      // 繪製驗證碼
      canvas2d.font = 'bolder 25px Roboto';
      canvas2d.fillStyle = 'black';
      canvas2d.fillText(this.captcha_code, 5, 30);

      // 繪製干擾線
      canvas2d.strokeStyle = 'black';
      canvas2d.beginPath();
      canvas2d.lineWidth = '1';
      canvas2d.moveTo(Math.floor(Math.random() * 70) + 5, 0);
      canvas2d.lineTo(Math.floor(Math.random() * 60) + 5, 25);
      canvas2d.moveTo(Math.floor(Math.random() * 70) + 5, 10);
      canvas2d.lineTo(Math.floor(Math.random() * 70) + 5, 28);
      canvas2d.stroke();

      // 繪製干擾點
      canvas2d.strokeStyle = 'black';
      canvas2d.beginPath();
      canvas2d.lineWidth = '1';
      for (let i = 0; i < 100; i += 1) {
        const bex = Math.floor(Math.random() * 78);
        const bey = Math.floor(Math.random() * 28);
        const tex = parseFloat((Math.random() * 2).toFixed(2));
        const tey = parseFloat((Math.random() * 2).toFixed(2));
        const enx = bex + tex;
        const eny = bey + tey;
        canvas2d.moveTo(bex, bey);
        canvas2d.lineTo(enx, eny);
      }
      canvas2d.stroke();
    },
    submit_form() {
      if (this.captcha_code === this.input_code) {
        this.mailbox.push(this.form);
      } else {
        alert('驗證碼錯誤');
      }
      this.create_canvas();
      this.create_form();
    },
  },
  created() {
    this.create_form();
  },
  mounted() {
    this.create_canvas();
  },
};
</script>
